// Button component - TSX file
// This tests .tsx (TypeScript with JSX) support

interface ButtonProps {
    label: string;
    variant?: 'primary' | 'secondary' | 'danger';
    disabled?: boolean;
    onClick?: () => void;
}

// Note: This is a simple JSX-like structure for demonstration
// In a real app, you'd use React, Preact, or similar
function createButton(props: ButtonProps): HTMLButtonElement {
    const { label, variant = 'primary', disabled = false, onClick } = props;

    const button = document.createElement('button');
    button.textContent = label;
    button.className = `btn btn-${variant}`;
    button.disabled = disabled;

    if (onClick) {
        button.addEventListener('click', onClick);
    }

    return button;
}

// Simple JSX-like factory (for testing TSX compilation)
function h(
    tag: string,
    props: Record<string, any> | null,
    ...children: (string | HTMLElement)[]
): HTMLElement {
    const element = document.createElement(tag);

    if (props) {
        for (const [key, value] of Object.entries(props)) {
            if (key === 'className') {
                element.className = value;
            } else if (key === 'onClick') {
                element.addEventListener('click', value);
            } else if (key.startsWith('data-')) {
                element.setAttribute(key, value);
            } else {
                (element as any)[key] = value;
            }
        }
    }

    for (const child of children) {
        if (typeof child === 'string') {
            element.appendChild(document.createTextNode(child));
        } else {
            element.appendChild(child);
        }
    }

    return element;
}

// Example usage with JSX-like syntax (after TSX compilation)
function renderButtonGroup(container: HTMLElement): void {
    const group = h('div', { className: 'button-group' },
        h('button', {
            className: 'btn btn-primary',
            onClick: () => console.log('Primary clicked!')
        }, 'Primary'),
        h('button', {
            className: 'btn btn-secondary',
            onClick: () => console.log('Secondary clicked!')
        }, 'Secondary'),
        h('button', {
            className: 'btn btn-danger',
            onClick: () => console.log('Danger clicked!')
        }, 'Danger')
    );

    container.appendChild(group);
}

console.log('[button.tsx] TSX component loaded');

export { createButton, h, renderButtonGroup, ButtonProps };
